
.labelled-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
  
}

.icon {
  
  object-fit: cover;
  
}

.icon-img {
  border-radius: 20px;
  overflow: hidden;
  width: 100px;
  height: 100px;
  object-fit: fill;
}